.profile__avatar{
  grid-column: 1;
  grid-row: 1;
  width: 120px;
  height: 120px;
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
  background-repeat: no-repeat;
  background-size: cover;
  color: transparent;
}

@media (max-width: 740px) {
  .profile__avatar{
    align-self: center;
  }
}